//mutation
export const setUser = ({commit},user) => {
  commit("userStatus",user)
}
//
export const setImage = ({commit},imageUrl) => {
  commit("userImageGet",imageUrl)
}
export const setToken = ({commit},userToken) => {
  commit("userToken",userToken)
}



