  display: flex;
  border-radius: 10px;
  background: #ddd;
  margin: 16px;
  flex-direction: row;
}
.index_switch_buttons .index_button {
  flex: 1;
  padding: 12px;
  border-radius: 10px;
  font-size: 16px;
  text-align: center;
}
.index_switch_buttons .checked {
  background: #efefef;
  font-weight: bold;
}

.number_container {
  padding: 10px;
  display: flex;
  flex: 1;
  width: 100%;
}
.number_block {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.number_block .title {
  font-size: 12px;
  color: #000;
  background: #f2f2f2;
  border-radius: 3px;
  padding: 2px;
  margin: 4px;
}
.number_block .increase_block {
  display: flex;
  flex-direction: row;
  font-size: 12px;
}
