<template>
  <body>
  <div class="root" style="background:#fff; padding:10px">
    <a class="card" title="Global epidemic" :bordered="false" style="width: 80%">
      <div>
        <div id="chart1"></div>
      </div>
    </a>
  </div>
</body>
</template>
<script>
// axios  axios 
// jsonp
import jsonp from 'jsonp';
// echarts
import echarts from 'echarts';

const option = {
  title: {
    text: 'Global'
  },
  tooltip: {
    trigger: 'axis'
  },
  legend: {
    data: ['Confirmed cases(except China)', 'Cumulative cure(except China)', 'Cumulative death(except China)', 'Suspected domestic cases(except China)']
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  toolbox: {
    feature: {
      saveAsImage: {}
    }
  },
  xAxis: {
    type: 'category',
    boundaryGap: false,
    data: ['', '', '', '', '', '', '']
  },
  yAxis: {
    type: 'value'
  },
  series: [
    {
      name: 'Confirmed cases(except China)',
      type: 'line',
      smooth: true,
      stack: '()',
      // `````data: [520, 132, 101, 134, 90, 230, 210],`````
      data: []
    },
    {
      name: 'Cumulative cure(except China)',
      type: 'line',
      smooth: true,
      stack: '()',
      // `````data: [520, 132, 101, 134, 90, 230, 210],`````
      data: []
    },
    {
      name: 'Cumulative death(except China)',
      type: 'line',
      smooth: true,
      stack: '()',
      // `````data: [520, 132, 101, 134, 90, 230, 210],`````
      data: []
    },
    {
      name: 'Suspected domestic cases(except China)',
      type: 'line',
      smooth: true,
      stack: '()',
      // `````data: [520, 132, 101, 134, 90, 230, 210],`````
      data: []
    }
  ]
};

export default {
  mounted () {
    this.getMsg();
    // this.$http.get('http://www.baidu.com', (res)=>{
    //   console.log(res.data)
    // })
    this.mycharts = echarts.init(document.getElementById('chart1'));
  },
  data () {
    return {
      mycharts: ''
    };
  },
  methods: {
    getMsg () {
      jsonp(
        'http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',
        (err, data) => {
          // console.log(data.data);
          // console.log(data.data.historylist);
          const list1 = data.data.otherhistorylist.map(item => {
            return {
              name: item.date,
              // 
              certain: item.certain,
              recure: item.recure,
              die: item.die,
              uncertain: item.uncertain
              // cn_susNum: item.cn_susNum,
              // cn_cureNum: item.cn_cureNum,
              // cn_deathNum: item.cn_deathNum,
              // wuhan_conNum: item.wuhan_conNum,
              // wuhan_deathNum: item.wuhan_deathNum,
              // wuhan_cureNum: item.wuhan_cureNum
            };
          })
          console.log(list1);
          // 
          option.series[0].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.certain
            }
          })
          option.series[1].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.recure
            }
          })
          option.series[2].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.die
            }
          })
          option.series[3].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.uncertain
            }
          })
          // 
          option.xAxis.data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.name
            }
          })
          // console.log(option.xAxis.data)
          // console.log(option.series[0].data)
          // mounted
          this.mycharts.setOption(option);
        }
      );
    }
  }
};
</script>

<style lang="less" scoped>
.root {
  display: flex;
  justify-content: center;
  align-items: center;
}
#chart1 {
  width: 100%;
  height: 800px;
  text-align: center;
}
</style>



