<template>
  <body>
  <div class="root" style="background:#fff; padding:10px">
    <a class="card" title="Domestic epidemic" :bordered="false" style="width: 80%">
      <div>
        <div id="chart1"></div>
      </div>
    </a>
  </div>
</body>
</template>
<script>
// axios  axios 
// jsonp
import jsonp from 'jsonp';
// echarts
import echarts from 'echarts';

const option = {
  title: {
    text: 'Domestic epidemic trend'
  },
  tooltip: {
    trigger: 'axis'
  },
  legend: {
    data: ['Newly diagnosed in China', 'Suspected domestic cases', 'New domestic imported cases']
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  toolbox: {
    feature: {
      saveAsImage: {}
    }
  },
  xAxis: {
    type: 'category',
    boundaryGap: false,
    data: ['', '', '', '', '', '', '']
  },
  yAxis: {
    type: 'value'
  },
  series: [
    {
      name: 'Newly diagnosed in China',
      type: 'line',
      smooth: true,
      stack: '',
      data: []
    },
    {
      name: 'Suspected domestic cases',
      type: 'line',
      smooth: true,
      stack: '',
      data: []
    },
    {
      name: '',
      type: 'line',
      smooth: true,
      stack: '',
      data: []
    },
    {
      name: 'New domestic imported cases',
      type: 'bar',
      smooth: true,
      stack: '',
      data: []
    }


  ]
};

export default {
  mounted () {
    this.getMsg();
    this.mycharts = echarts.init(document.getElementById('chart1'));
  },
  data () {
    return {
      mycharts: ''
    };
  },
  methods: {
    getMsg () {
      jsonp(
        'http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',
        (err, data) => {
          // console.log(data.data);
          // console.log(data.data.historylist);
          const list1 = data.data.historylist.map(item => {
            return {
              name: item.date,
              // 
              cn_conadd: item.cn_conadd,
              cn_susNum: item.cn_susNum,

              cn_addjwsrNum: item.cn_addjwsrNum,

            };
          })
          // console.log(list1);
          // 
          // 
          option.series[0].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.cn_conadd
            }
          })
           // 
          option.series[1].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.cn_susNum
            }
          })
          // 

          option.series[3].data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.cn_addjwsrNum
            }
          })

          // 
          option.xAxis.data = list1.slice(0, 30).reverse().map(item => {
            return {
              value: item.name
            }
          })
          // console.log(option.xAxis.data)
          // console.log(option.series[0].data)
          // mounted
          this.mycharts.setOption(option);
        }
      );
    }
  }
};
</script>

<style lang="less" scoped>
.root {
  display: flex;
  justify-content: center;
  align-items: center;
}
#chart1 {
  width: 100%;
  height: 800px;
  text-align: center;
}
</style>



